import { Command } from "./command.js";
/**
 * @see https://redis.io/commands/hset
 */
export class HSetCommand extends Command {
    constructor([key, kv], opts) {
        super([
            "hset",
            key,
            ...Object.entries(kv).flatMap(([field, value]) => [field, value]),
        ], opts);
    }
}
