import { Command } from "./command.js";
/**
 * @see https://redis.io/commands/json.arrtrim
 */
export class JsonArrTrimCommand extends Command {
    constructor(cmd, opts) {
        const path = cmd[1] ?? "$";
        const start = cmd[2] ?? 0;
        const stop = cmd[3] ?? 0;
        super(["JSON.ARRTRIM", cmd[0], path, start, stop], opts);
    }
}
